/* Pricing Section */
.pricing-section {
  text-align: left;
}

.license {
  float: left;
  width: 33%;
  padding: 30px;
  border-radius: 5px;
  position: relative;
  background-color: #FFF;
}

.license-regular {
  z-index: 10;
  border: 1px solid #EEE;
}

.most-popular {
  text-align: center;
  position: absolute;
  top: -1px;
  width: calc(100% + 2px);
  left: -1px;
  background-color: #056FFF;
  color: #FFF;
  border-radius: 5px 5px 0 0;
  padding: 5px 0;
  border-bottom: 1px solid rgba(0,0,0,.2);
}

h5 {
  text-align: left;
  font-size: 20px;
  color: #000;
  font-weight: 500;
  letter-spacing: -1px;
  margin-bottom: 5px;
}

.license p {
  text-align: left;
  color: #999;
}

.license button {
  margin: 20px 0;
}

.license-features strong {
  font-weight: 500;
}

.license-features p {
  margin: 15px 0;
}
.license-features p.blue {
  color: #066FFF;
}

.license-price {
  text-align: left;
  font-size: 15px;
  font-weight: bold;
  color: #999;
}

.license-price strong {
  font-size: 28px;
  color: #333;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width:100%;
  height: 100%;
  z-index: 100;
  background: rgba(0, 0, 0, 0.6);
}

.modal-main {
  position:fixed;
  background: white;
  width: 50%;
  height: auto;
  top:50%;
  left:50%;
  transform: translate(-50%,-50%);
}

.display-block {
  display: block;
}

.display-none {
  display: none;
}