body {
  margin: 0;
  padding: 0;
  font-family: 'Gibson-webfont', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html {
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

ul li {
  float: left;
}

.container {
  position: relative;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

button {
  outline: none;
  color: #FFFFFF;
  background: #066FFF;
  padding: 8px;
  border-radius: 5px;
  cursor: pointer;
  border: 1px solid transparent;
}

button.large {
  font-size: 15px;
  font-weight: 500;
  letter-spacing: -1px;
  padding: 8px 25px;
}

button.outline {
  background-color: transparent;
  color: #066FFF;
  border: 1px solid #066FFF;
}

.page {
  padding-top: 60px;
}

h1 {
  font-size: 46px;
  font-weight: 600;
  color: #000;
  letter-spacing: -1px;
  margin-bottom: 4px;
  text-align: center;
}

p.subtext {
  color: rgb(133, 133, 133);
  font-size: 24px;
  font-weight: 300;
  width: 660px;
  margin: 0 auto;
  margin-bottom: 40px;
  text-align: center;
}

header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  border-bottom: 1px solid #f7f7f7;
  box-shadow: 0 4px 60px 12px rgba(0, 0, 0, 0.01);
  background-color: #FFF;
  height: 60px;
  z-index: 999;
}

ul a {
  font-size: 14px;
  color: #858585;
  float: left;
  margin-left: 15px;
  padding: 10px;
}

ul a:hover {
  color: #056FFF;
}

ul a:first-of-type {
  margin-left: 0;
}

.link-section {
  position: absolute;
  left: 0;
  top: 10px;
}

/* Buy Section */
.buy-section {
  position: absolute;
  right: 0;
  top: 12px;
}

.buy-promo {
  float: left;
  text-align: right;
  padding-right: 15px;
  padding-top: 4px;
}

.buy-promo-name {
  font-size: 10px;
  font-weight: 600;
  color: #A5A5A5;
  text-transform: uppercase;
}

.buy-promo-price {
  font-size: 13px;
  color: #000000;
}

button.buy-now {
  font-weight: bold;
  letter-spacing: -1px;
  padding: 0 18px;
  height: 32px;
  font-size: 11px;
  line-height: 32px;
  text-transform: uppercase;
  color: #ffffff;
  text-align: center;
  background: #056FFF;
  border: 0;
  border-radius: 2px;
  transition: color 0.15s cubic-bezier(0.4, 0, 0.2, 1),
              background-color 0.15s cubic-bezier(0.4, 0, 0.2, 1),
              border-color 0.15s cubic-bezier(0.4, 0, 0.2, 1);
}

.logo {
  width: 32px;
  padding: 6px;
  cursor: pointer;
}

header .container {
  text-align: center;
}

.full-preview-section {
  float: left;
  width: 100%;
}

h2 {
  width: 100%;
  text-transform: capitalize;
}

.icon {
  width: 68px;
  margin: 10px;
}

.categories-section {
  width: 220px;
  background-color: #FFF;
  position: -webkit-sticky;
  position: sticky;
  top: 120px;
  float: left;
  overflow: scroll;
  text-align: center;
  z-index: 99;
  height: calc(100vh - 120px);
}

.categories-section h2 {
  text-align: left;
}

.icon-type-section {
  width: 100%;
  background-color: #FFF;
  border-top: 1px solid #EEE;
  border-bottom: 1px solid #EEE;
  position: -webkit-sticky;
  position: sticky;
  top: 59px;
  float: left;
  text-align: center;
  z-index: 99;
}

.icon-type {
  cursor: pointer;
  display: inline-block;
  border-bottom: 3px solid transparent;
  font-size: 14px;
  padding: 20px;
}

.icon-type.selected {
  cursor: default;
  font-weight: 600;
  border-bottom: 3px solid #056FFF;
}

.category {
  color: #999;
  cursor: pointer;
  padding: 5px;
  text-align: left;
}

.category:hover {
  color: #000;
}

.icon-section-wrapper {
  width: calc(100% - 220px);
  float: left;
}

button.download {
  font-size: 16px;
  color: #FFF;
  font-weight: 500;
  letter-spacing: -1px;
  padding: 12px 25px;
  border-radius: 5px;
  cursor: pointer;
  margin: 0 10px 10px 10px;
  border: 1px solid transparent;
}

button.download-free {
  color: #056FFF;
  background-color: transparent;
  border: 1px solid #056FFF;
}

.button-container {
  text-align: center;
  margin-bottom: 40px;
}

.free-input-container {
  border: 1px solid #DDD;
  width: 300px;
  margin: 0 auto;
  border-radius: 3px;
  display: -webkit-flex;
  display: flex;
  padding: 2px;
}

.free-input-container.focused {
  border: 1px solid #066FFF;
}

.free-input {
  border-radius: 3px;
  border: none;
  padding: 8px;
  font-size: 12px;
  -webkit-flex: 1 1;
          flex: 1 1;
  outline: none;
}

.free-btn {
  border-radius: 3px;
}

.title-section {
  padding-bottom: 60px;
}

.email-sent {
  color: #4CD139;
}

.email-sent.error {
  color: #e23349;
}

.email-sent.error button {
  margin-top: 10px;
}

.loading img {
  width: 70px;
}

.sidebar {
  position: fixed;
  right: 0;
  width: 480px;
  background-color: #FFF;
  z-index: 999;
  height: 100%;
  padding: 20px;
  top: 0;
  -webkit-transform: translate3d(485px, 0, 0);
          transform: translate3d(485px, 0, 0);
  border-left: 1px solid #eaeaea;
  transition: -webkit-transform .3s ease-in;
  transition: transform .3s ease-in;
  transition: transform .3s ease-in, -webkit-transform .3s ease-in;
  box-shadow: -1px 0 3px rgba(0,0,0,.05);
}

.sidebar.visible {
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}

.sidebar-bg {
  visibility: hidden;
  background-color: rgba(255,255,255,.9);
  z-index: 999;
  position: fixed;
  left: 0;
  right: 400px;
  top: 0;
  height: 100%;
  transition: visibility 0s, opacity .3s ease-in;
}

.sidebar-bg.visible {
  visibility: visible;
  opacity: 1;
}

.checkout-close {
  position: absolute;
  right: 20px;
  top: 20px;
  font-size: 15px;
  width: 30px;
  height: 30px;
  border-radius: 100px;
  border: 2px solid #EEE;
  text-align: center;
  line-height: 24px;
  color: #333;
  cursor: pointer;
}

/* checkout inner */
.sidebar h3 {
  color: #333;
}

.sidebar input.email {
  width: 100%;
  font-size: 15px;
  border: none;
  border-bottom: 1px solid #EEE;
  padding: 10px 0;
}

.sidebar p {
  font-size: 13px;
  color: #999;
}

.sidebar .newsletter {
  position: absolute;
  bottom: 70px;
  left: 0;
  width: 100%;
  padding: 20px 120px 20px 20px;
  border-top: 1px solid #EEE;
}

.checkout-sidebar .newsletter-checkbox {
  float: left;
  width: 100%;
  margin-top: 10px;
}

.sidebar .newsletter-checkbox input {
  float: left;
  position: relative;
  top: 4px;
  cursor: pointer;
}

.sidebar .newsletter-checkbox label {
  float: left;
  font-size: 13px;
  margin-left: 10px;
}

.sidebar h4 {
  color: #333;
  line-height: 130%;
}

.sidebar .checkout-footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 70px;
  left: 0;
  border-top: 1px solid #EEE;
  padding: 20px;
}

.sidebar button.checkout-btn {
  position: absolute;
  right: 20px;
  top: 17px;
  font-size: 15px;
  background-color: #333;
  padding: 10px 25px;
  border-radius: 100px;
  border: none;
  color: #FFF;
}


/* Pricing Section */
.pricing-section {
  text-align: left;
}

.license {
  float: left;
  width: 33%;
  padding: 30px;
  border-radius: 5px;
  position: relative;
  background-color: #FFF;
}

.license-regular {
  z-index: 10;
  border: 1px solid #EEE;
}

.most-popular {
  text-align: center;
  position: absolute;
  top: -1px;
  width: calc(100% + 2px);
  left: -1px;
  background-color: #056FFF;
  color: #FFF;
  border-radius: 5px 5px 0 0;
  padding: 5px 0;
  border-bottom: 1px solid rgba(0,0,0,.2);
}

h5 {
  text-align: left;
  font-size: 20px;
  color: #000;
  font-weight: 500;
  letter-spacing: -1px;
  margin-bottom: 5px;
}

.license p {
  text-align: left;
  color: #999;
}

.license button {
  margin: 20px 0;
}

.license-features strong {
  font-weight: 500;
}

.license-features p {
  margin: 15px 0;
}
.license-features p.blue {
  color: #066FFF;
}

.license-price {
  text-align: left;
  font-size: 15px;
  font-weight: bold;
  color: #999;
}

.license-price strong {
  font-size: 28px;
  color: #333;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width:100%;
  height: 100%;
  z-index: 100;
  background: rgba(0, 0, 0, 0.6);
}

.modal-main {
  position:fixed;
  background: white;
  width: 50%;
  height: auto;
  top:50%;
  left:50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
}

.display-block {
  display: block;
}

.display-none {
  display: none;
}
footer {
  margin-top: 40px;
  float: left;
  width: 100%;
  background: #fafafa;
  padding: 40px;
  position: relative;
}

.description {
  text-align: left;
  color: #999;
  font-size: 14px;
  width: 50%;
}

.powered-by-stripe {
  opacity: .5;
  width: 120px;
  position: absolute;
  right: 0;
  top: 0;
}


section {
  padding-top: 80px;
  width: 100%;
  float: left;
  text-align: center;
}

/* Hero section */
.hero-logo {
  width: 100%;
}

/* Preview Section*/
.box-container {
  padding: 10px;
  width: 50%;
  float: left;
}

.box {
  background: #FAFAFA;
  padding: 30px;
}

.box p {
  color: #999;
  font-size: 18px;
}

.box h4 {
  font-weight: 400;
  font-size: 26px;
  color: #000;
  letter-spacing: -1px;
  margin-top: 0;
  margin-bottom: 10px;
}



.logo {
  vertical-align: middle;
}

#checkout-label {
  float: right;
  margin-top: 20px;
}

#input-container {
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.input-field {
  margin-top: 5px;
  width: 250px;
}

#child1 {
  margin-top: 5px;
  display: -webkit-flex;
  display: flex;
  width: 250px;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.child1-input {
  width: 120px;
}

#input-label {
  width: 200px;
  font-size: 12px;
  margin-top: 20px;
}

#paybutton {
  width: 250px;
  background: black;
  margin-top: 30px;
}

.StripeElement {
  width: 250px;
}
